.geometry-header{
  background-color: rgba(0, 0, 0, 1);
  display: grid;
  align-items: center;
  color: rgba(255, 255, 255, 1);
  display: flex;
  gap: 10px;
}
.gnrt-logo {
  padding-left: 20px;
  height: 45px;
  cursor: pointer;
}
.logo-name {
  font-size: 1.4em; 
  transform: translateX(2px);
}
@media screen and (max-width: 600px) {
  .gnrt-logo {
    padding-left: 10px;
  }
}